import {connect} from "react-redux";
import React, {Component} from "react";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {OraButton} from "eshop/components/OraCommonComponents";
import {getCimConsistentCheckoutErrors, getCustomerData, getSapReservationNumber} from "../../selectors";
import {getCartCode} from "eshop/modules/cart/selectors";
import {dismissBackendValidationErrors, gotoCartSummary, cancelOrderWithRedirect, closeCimConsistentErrorModal} from "../../actions/app";
import {removeFromCartAndRedirect} from "eshop/modules/cart/actions/cart";

class CimCartConsistentErrorModalView extends Component {

    onClose() {
        this.props.dismiss();
    }

    onRemoveAllFromCart() {
        if (!this.props.sapReservationNumber || this.props.sapReservationNumber.length === 0) {
            this.props.removeFromCartAndRedirect();
        } else {
            this.props.cancelOrderWithRedirect();
        }
    }

    onCloseModal() {
        this.props.closeCimConsistentErrorModal();
    }

    renderInfo() {
        return (
            <span>
                <span>{this.props.errors && this.props.errors.length > 0 ? this.props.textInfo:""}</span>
            </span>
        );
    }

    render() {
        return (
            <OraModal showClose={true}  open={this.props.errors && this.props.errors.length > 0}
             onClose={this.onClose.bind(this)} size="medium" id={'cim-consistent-error-modal'}>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-12">
                            {this.renderInfo()}
                        </div>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 large-offset-by-4">
                        <OraButton onClick={this.onRemoveAllFromCart.bind(this)} className="u-w-100">
                            Usu≈Ñ koszyk
                        </OraButton>
                    </div>
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 u-small-spacing">
                        <OraButton onClick={this.onCloseModal.bind(this)} className="u-w-100" type="secondary">
                            Dalej
                        </OraButton>
                    </div>
                </div>
            </OraModal>);
    }
}

let mapStateToProps = state => ({
    errors: getCimConsistentCheckoutErrors(state),
    sapReservationNumber: getSapReservationNumber(state)
});

let mapDispatchToProps = dispatch => ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    action: () => dispatch(gotoCartSummary()),
    cancelOrderWithRedirect: () => dispatch(cancelOrderWithRedirect()),
    closeCimConsistentErrorModal: () => dispatch(closeCimConsistentErrorModal()),
    removeFromCartAndRedirect: () => dispatch(removeFromCartAndRedirect())
});

const CimCartConsistentErrorModal = connect(mapStateToProps, mapDispatchToProps)(CimCartConsistentErrorModalView);
export default CimCartConsistentErrorModal;
